@import "custom";

.Today {
  form .searchCity {
    width: 100%;
    border-radius: 99px;
    border: 0;
    padding: 0 1em;
    height: 2.5em;
    background-color: $clr-dark;
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.6);
  }

  form .searchCity:focus {
    outline: none;
  }

  form .searchCity::placeholder {
    color: rgba(238, 238, 238, 0.6);
  }

  .city-date-time-wrapper {
    .city {
      font-size: 2.5em;
      font-weight: 900;

      .country {
        font-size: 1rem;
        font-weight: 400;
      }
    }

    .date-time {
      display: flex;
      gap: 1.5em;

      span {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5em;
        color: $fnt-clr-white-opq;
      }
    }
  }

  .weather-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1em;

    .cloudy {
      font-variation-settings: "FILL" 1, "wght" 400, "GRAD" 0, "opsz" 48;
      font-size: min(40vw, 300px);
      background: -webkit-linear-gradient(45deg, #eee, rgb(0, 255, 255));
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .temperatures-weather {
      .temp {
        font-size: 4em;
        font-weight: 700;
        position: relative;

        span {
          span {
            font-size: 0.75em;
            position: relative;
            bottom: 0.4em;
          }
        }
      }

      .min-max-temp {
        display: flex;
        justify-content: space-between;

        .min-temp,
        .max-temp {
          display: flex;
          align-items: center;
          font-variation-settings: "FILL" 1, "wght" 400, "GRAD" 0, "opsz" 48;
          position: relative;
        }

        .minTemp {
          color: rgb(165, 255, 255);
        }
        .maxTemp {
          color: rgb(255, 160, 160);
        }
      }
    }
  }
}
